html {
    scroll-snap-stop: y mandatory;
}

.App {
    background-color: white;
    height: 100vh;
    display: grid;
    place-items: center;
}

.app__videos {
    position: relative;
    height: 90vh;
    overflow: scroll;
    /* width: 80%; */
    width: 288px;
    max-width: 500px;
    scroll-snap-type: y mandatory;
    /* box-shadow: 0 5px 10px black; */
    /* box-shadow:  0 3px 1px -2px #00000033,0 6px 13px 0 #00000024,0 7px 17px 0 #0000001f; */
    box-shadow: 0px 0px 1px -1px rgba(0, 0, 0, 0.2),
    0px 2px 9px 2px rgba(0, 0, 0, 0.14), 0px 1px 16px 1px rgba(0, 0, 0, 0.12);
}


/* Hide scrollbar for Chrome, Safari adn Opera */
.app__videos::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar ofr IE, Edge, and Firefox */
.app__vedios {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
