.video_footer {
    position: relative;
    bottom: 150px;
    margin-left: 15px;
    color: white;
    display: flex;
}

.video_footer__text {
    flex: 1;
}

.video_footer__ticker {
    padding-top: 7px;
    font-size: 14px;
}

.video_footer__icon {
    position: absolute;
}
  
.video_footer__ticker > .ticker {
    height: fit-content;
    margin-left: 30px;
    width: 60%;
}
  
.video_footer__record {
    animation: spinTheRecord infinite 5s linear;
    height: 50px;
    filter: invert(1);
    position: absolute;
    bottom: 0;
    right: 10px;
}
  
.video_footer__text > h3 {
    padding-bottom: 20px;
}

.video_footer__text > p {
    padding-bottom: 30px;
}
  
.tickker {
    padding-top: 7px;
    font-size: 12px;
}
  
@keyframes spinTheRecord {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}